	js:
		"\n// Part 3: Here we use the helper function 'drawGlitch', which takes in an image and draws a glitchy version of it.\n\n\n\nvar liftImg\nvar tokyoImg\n\n\nfunction preload(){\n  // 'liftImg' is imported from the local file 'lift.png'\n  liftImg = loadImage(\"lift.png\")\n  \n  // 'tokyoImg' is imported from the web using a url\n  tokyoImg = loadImage('https://i.imgur.com/JfIpaLT.jpg')\n}\n\n\n// HOW TO: Using Assets\n\n\nfunction setup(){\n  createCanvas(360, 200)\n}\n\nfunction drawGlitch(img = liftImg){\n  // this draws a glitched version of the passed in 'img'\n  img.loadPixels()\n  \n  for (let i = 0; i < img.pixels.length; i=i+4) {\n    if (img.pixels[i+10] != 0) {\n      img.pixels[i+0] = img.pixels[i+100]   // red\n    } else {\n      img.pixels[i+0] = img.pixels[i-100]   // red\n    }\n  }\n  \n  img.updatePixels()\n  image(img, 0, 0, width, height)\n}\n\nfunction draw(){\n  drawGlitch(tokyoImg)\n  //drawGlitch(liftImg)\n}\n\n\n// Part 1: Just like in normal p5 sketches, you can use local Files and Assets. In stamper, these take the form of special 'file' and 'media' Stamps.\n\n\n\n// Part 2: Here we use p5's 'preload' function to import two types of images, one from a local file and one from the web.\n\n\n\n// By default, 'drawGlitch' takes in 'liftImg'. Notice what it draws though when we pass in 'tokyoImg'.\n\n",
