		"\n/* Part 2: Green Lines \n    -> point to Stamps loading that file or asset\n*/\n\n\nfunction setup(){\n  createCanvas(200, 200)\n  background(255)\n  \n  // load the image\n  gradientImg = loadImage('gradient.png')\n}\n\n\n/* Part 3: Grey Lines\n    -> point to Stamps that by default use this built in p5 function\n*/\n\n\n\nvar gradientImg\nvar numRows = 1\nvar numCols = 1\n\n\n\n/* Part 4: Blue Lines \n    -> point to Stamps using that variable or function\n*/\n\n\nfunction mousePressed(){\n  // set number of columns and rows\n  numRows = Math.floor(random(1, 10))\n  numCols = Math.floor(random(1, 10))\n  \n  // draw a debug rectangle\n  background(255)\n  rect(0, 0, width/numCols, height/numRows)\n}\n\nfunction draw(){\n  // get width and height of each gradient\n  var imgWidth = width/numCols\n  var imgHeight = height/numRows\n  \n  // draw the gradients\n  for(var x = 0; x < width; x += imgWidth){\n    for(var y = 0; y < height + imgHeight; y += imgHeight){\n      \n     image(gradientImg,x, y, imgWidth, imgHeight)\n     \n    }\n  }\n}\n\n\n/* Part 5: Pink Lines \n    -> point to Stamps hearing that listener\n*/\n\n\n\n// HOW TO: Understanding Lines\n\n\n\n/* Part 1: Stamper has a 'lines' mode, which can be used to see how your Stamps are connected. There are a few different types of lines. However, in general:\n    - a line from A -> B means Stamp B references code in Stamp A\n*/\n\n",
